.heroplugin {
  position: relative;
  padding: 200px 0;
  background-image: url("../img/bg-img.jpg");
  background-size: cover;
  background-position: center center; }

.hero--overlay {
  box-shadow: inset 0 0 0 1000px rgba(19, 18, 22, 0.5); }

.hero--gradient {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(73, 165, 191, 0.5)), url("../img/bg-img.jpg"); }

.hero--gradient.gradient--totop {
  background: linear-gradient(to top, rgba(255, 255, 255, 0.5), rgba(73, 165, 191, 0.5)), url("../img/bg-img.jpg"); }

.hero--gradient.gradient--tobottom {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(73, 165, 191, 0.5)), url("../img/bg-img.jpg"); }

.hero--gradient.gradient--toleft {
  background: linear-gradient(to left, rgba(255, 255, 255, 0.5), rgba(73, 165, 191, 0.5)), url("../img/bg-img.jpg"); }

.hero--gradient.gradient--toright {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.5), rgba(73, 165, 191, 0.5)), url("../img/bg-img.jpg"); }

.hero--fixed {
  background-attachment: fixed; }

.hero--gradient.hero--fixed {
  background-attachment: fixed, fixed; }

.hero--lefttop {
  background-position: left top; }

.hero--gradient.hero--lefttop {
  background-position: left top, left top;
  background-size: cover, cover; }

.hero--centertop {
  background-position: center top; }

.hero--gradient.hero--centertop {
  background-position: center top, center top;
  background-size: cover, cover; }

.hero--righttop {
  background-position: right top; }

.hero--gradient.hero--righttop {
  background-position: right top, right top;
  background-size: cover, cover; }

.hero--leftcenter {
  background-position: left center; }

.hero--gradient.hero--leftcenter {
  background-position: left center, left center;
  background-size: cover, cover; }

.hero--centercenter {
  background-position: center center; }

.hero--gradient.hero--centercenter {
  background-position: center center, center center;
  background-size: cover, cover; }

.hero--rightcenter {
  background-position: right center; }

.hero--gradient.hero--rightcenter {
  background-position: right center, right center;
  background-size: cover, cover; }

.hero--leftbottom {
  background-position: left bottom; }

.hero--gradient.hero--leftbottom {
  background-position: left bottom, left bottom;
  background-size: cover, cover; }

.hero--centerbottom {
  background-position: center bottom; }

.hero--gradient.hero--centerbottom {
  background-position: center bottom, center bottom;
  background-size: cover, cover; }

.hero--rightbottom {
  background-position: right bottom; }

.hero--gradient.hero--rightbottom {
  background-position: right bottom, right bottom;
  background-size: cover, cover; }
